import React, { useMemo, FC } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Card, Grid, Typography, makeStyles } from '@material-ui/core';
import { Theme } from 'src/theme';
import Label from 'src/components/Label';
import LoadingBox from 'src/components/LoadingBox';
import currency from 'currency.js';
import { BigNumber } from '@ethersproject/bignumber';
import { formatBalance, formatMoney } from 'src/utils/format';
import { useTranslation } from 'react-i18next';
import { useRecoilValue } from 'recoil';
import { tokenPriceLoadingAtom } from 'src/atoms';
import { BTCST } from 'src/constants/token';
import isEmpty from 'lodash/isEmpty';
import { getPriceBySymbol } from 'src/utils/price';

interface OverviewProps {
  className?: string;
  tvl: any;
  txInfo: any;
  tokenPrice: any;
  tbtcTotalSupply?: any;
  btcstTotalSupply?: any;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  item: {
    padding: theme.spacing(3),
    textAlign: 'center',
    [theme.breakpoints.up('md')]: {
      '&:not(:last-of-type)': {
        borderRight: `1px solid ${theme.palette.divider}`
      }
    },
    [theme.breakpoints.down('sm')]: {
      '&:not(:last-of-type)': {
        borderBottom: `1px solid ${theme.palette.divider}`
      }
    }
  },
  valueContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  label: {
    marginLeft: theme.spacing(1)
  },
  tau: {
    'text-transform': 'initial'
  }
}));

const Overview: FC<OverviewProps> = ({
  tvl,
  tokenPrice,
  tbtcTotalSupply,
  btcstTotalSupply,
  txInfo,
  className,
  ...rest
}) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);
  //const LOCKED_Supply = BigNumber.from(4778006+500000).mul(1e17+"");
  const LOCKED_Supply = BigNumber.from(0).mul(1e17 + '');

  const totalInfo = useMemo(() => {
    const result = {
      txs: 0,
      holders: 0
    };
    console.log('txInfo', txInfo.result);
    if (txInfo?.result?.data?.length) {
      txInfo.result.data.forEach(item => {
        result.txs += item.count;
        result.holders += item.sender_count;
      });
    }
    return result;
  }, [txInfo.result]);

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Grid alignItems="center" container justify="space-between">
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.tvl')}
          </Typography>

          <div className={classes.valueContainer}>
            <LoadingBox loading={tvl.loading} p={0} size={'25px'}>
              <Typography variant="h3" color="textPrimary">
                {tvl.result ? currency(tvl.result).format() : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.btcst.price')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={tokenPriceLoading} p={0} size={'25px'}>
              <Typography variant="h3" color="textPrimary">
                {!isEmpty(tokenPrice)
                  ? currency(
                      getPriceBySymbol(tokenPrice, BTCST.symbol)
                    ).format()
                  : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.dapp.activeAddress')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={txInfo.loading} p={0} size={'25px'}>
              <Typography variant="h3" color="textPrimary">
                {totalInfo.holders || '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.dapp.txCount')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={txInfo.loading} p={0} size={'25px'}>
              <Typography variant="h3" color="textPrimary">
                {totalInfo.txs || '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
      </Grid>
    </Card>
  );
};

Overview.propTypes = {
  className: PropTypes.string
};

export default Overview;
