import { BigNumber, FixedNumber, utils } from 'ethers';
import { useWeb3React } from '@web3-react/core';
import { useAsync } from 'react-async-hook';
import {
  useFarmBTCSTContract,
  useTBTCContract,
  useTBTCESTPolicyContract
} from './useContract';
import { contractConfig } from 'src/config';
import {
  getTotalStakedBTCST,
  totalSupply,
  getNextRebaseDate,
  viewFarmBasicInfo,
  getDailyRecords,
  getTVL,
  getTokenPrice,
  TARGET_INITIAL_SUPPLY,
  alreadyMinedReward,
  merkleTotalAlreadyClaimed,
  getPoolInfo,
  getTXInfo,
  getTokensInfo
} from 'src/service';
import { useEffect, useMemo } from 'react';
import {
  getERC20Contract,
  getFarmBTCSTContract,
  getTBTCChefContract,
  getTBTCESTPolicyContract
} from 'src/utils/contract';
import { formatBalance } from 'src/utils/format';
import { useDailyPool } from './useDailyPool';
import { BTCB, BTCST, FarmingPidMap, TokenList, FarmingTDogePidMap } from 'src/constants/token';
import { tokenPriceListAtom } from 'src/atoms';
import { useRecoilValue } from 'recoil';
import isEmpty from 'lodash/isEmpty';
import { getPriceBySymbol } from 'src/utils/price';

const BATCH_LOAD_RECORD_LIMIT = 5;
const RAMOUNT_DIVIDER = BigNumber.from(1e8).pow(BigNumber.from(4));

const getStakeAmount = async (contract: any, symbol: string, map: {[key: string]: number}) => {
  const pid = map[symbol]; 
  const poolInfo = await getPoolInfo(
    contract,
    pid
  );
  let sourceStakeAmount = poolInfo.totalDepositAmount;
  // const stakedTokenShrinkFactor = poolInfo.stakedTokenShrinkFactor;
  // if (stakedTokenShrinkFactor > 0) {
  //   sourceStakeAmount = sourceStakeAmount
  //     .mul(stakedTokenShrinkFactor)
  //     .div(BigNumber.from(1e12 + ''));
  // }

  return {
    symbol,
    sourceStakeAmount
  };
};

export const useStatsState = () => {
  const { account, library } = useWeb3React();
  const tokenPrice = useRecoilValue(tokenPriceListAtom);

  const { apy } = useDailyPool(BTCST, BTCB);

  const TBTCContract = useMemo(() => {
    return getERC20Contract(contractConfig.TBTC, library, account);
  }, [account, library]);

  const TDOGEContract = useMemo(() => {
    return getERC20Contract(contractConfig.TDOGE, library, account);
  }, [account, library]);

  const BTCSTContract = useMemo(() => {
    return getERC20Contract(contractConfig.STOKEN, library, account);
  }, [library, account]);

  const BTCBContract = useMemo(() => {
    return getERC20Contract(contractConfig.RTOKEN, library, account);
  }, [library, account]);

  const FarmBTCContract = useMemo(() => {
    return getFarmBTCSTContract(contractConfig.FARM, library, account);
  }, [library, account]);

  const FarmBTCSTContract = useMemo(() => {
    return getFarmBTCSTContract(contractConfig.FARM_BTCST, library, account);
  }, [library, account]);

  const TBTCChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TBTC_CHEF, library, account);
  }, [account, library]);

  const TDOGEChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TDOGE_CHEF, library, account);
  }, [account, library]);

  const TBTCESTPolicyContract = useMemo(() => {
    return getTBTCESTPolicyContract(
      contractConfig.TBTC_POLICY,
      library,
      account
    );
  }, [account, library]);


  const TDOGEESTPolicyContract = useMemo(() => {
    return getTBTCESTPolicyContract(
      contractConfig.TDOGE_POLICY,
      library,
      account
    );
  }, [account, library]);

  const BTCBPoolStakeAmount = useAsync<BigNumber>(() => {
    return BTCSTContract.balanceOf(contractConfig.FARM);
  }, [BTCSTContract]);

  const BTCSTPoolStakeAmount = useAsync<BigNumber>(() => {
    return BTCSTContract.balanceOf(contractConfig.FARM_BTCST);
  }, [BTCSTContract]);

  const totalMinedBTC = useAsync<BigNumber>(() => {
    return FarmBTCContract.viewAllTimeTotalMined();
  }, [FarmBTCContract]);

  const totalMinedBTCST = useAsync<BigNumber>(() => {
    return FarmBTCSTContract.viewAllTimeTotalMined();
  }, [FarmBTCSTContract]);

  const totalInPoolBTC = useAsync<BigNumber>(() => {
    return BTCBContract.balanceOf(contractConfig.FARM);
  }, [BTCBContract]);

  const tvl = useAsync(async () => {
    if (
      !BTCBPoolStakeAmount.result ||
      !BTCSTPoolStakeAmount.result ||
      isEmpty(tokenPrice)
    ) {
      return '';
    }

    const data: {
      symbol: string;
      sourceStakeAmount: BigNumber;
    }[] = await Promise.all(
      Object.keys(FarmingPidMap).map(symbol => {
        return getStakeAmount(TBTCChefContract, symbol, FarmingPidMap);
      })
    );

    const dogeData: {
      symbol: string;
      sourceStakeAmount: BigNumber;
    }[] = await Promise.all(
      Object.keys(FarmingTDogePidMap).map(symbol => {
        return getStakeAmount(TDOGEChefContract, symbol, FarmingTDogePidMap);
      })
    );

    const list = data.concat(dogeData).concat([
      { symbol: BTCST.symbol, sourceStakeAmount: BTCBPoolStakeAmount.result },
      { symbol: BTCST.symbol, sourceStakeAmount: BTCSTPoolStakeAmount.result }
    ]);

    let result = FixedNumber.from(0);

    list.forEach(item => {
      const p = getPriceBySymbol(tokenPrice, item.symbol);
      result = result.addUnsafe(
        FixedNumber.fromString(
          utils.formatUnits(
            item.sourceStakeAmount,
            TokenList[item.symbol].decimals
          )
        ).mulUnsafe(FixedNumber.fromString(String(p)))
      );
    });

    return result.toString();
  }, [
    BTCBPoolStakeAmount.result,
    BTCSTPoolStakeAmount.result,
    tokenPrice,
    TBTCChefContract,
    TDOGEChefContract
  ]);

  const txInfo = useAsync(getTokensInfo, []);

  const tbtcTotalSupply = useAsync<BigNumber>(() => totalSupply(TBTCContract), [
    TBTCContract
  ]);

  const tdogeTotalSupply = useAsync<BigNumber>(
    () => totalSupply(TDOGEContract),
    [TDOGEContract]
  );

  const btcstTotalSupply = useAsync<BigNumber>(() => {
    return totalSupply(BTCSTContract);
  }, [BTCSTContract]);

  const totalMinted = useAsync<BigNumber>(
    () => TBTCChefContract.totalMinted(),
    [TBTCChefContract]
  );

  const farmBTCBasicInfo = useAsync<any>(
    () => viewFarmBasicInfo(FarmBTCContract),
    [FarmBTCContract]
  );

  const farmBTCSTBasicInfo = useAsync<any>(
    () => viewFarmBasicInfo(FarmBTCSTContract),
    [FarmBTCSTContract]
  );

  const chefStats = useAsync<any>(async () => {
    const target = await TARGET_INITIAL_SUPPLY(TBTCChefContract);
    const mined = await alreadyMinedReward(TBTCChefContract);
    const airdropClaimed = await merkleTotalAlreadyClaimed(TBTCChefContract);
    const balanceForChef = await TBTCContract.balanceOf(
      contractConfig.TBTC_CHEF
    );
    const poolInfo = await getPoolInfo(TBTCChefContract, 2);
    const totalDepositTBTCAmount = poolInfo.totalDepositAmount;
    let stakedTBTC = totalDepositTBTCAmount;
    const stakedTokenShrinkFactor = poolInfo.stakedTokenShrinkFactor;
    if (stakedTokenShrinkFactor > 0) {
      stakedTBTC = stakedTBTC
        .mul(stakedTokenShrinkFactor)
        .div(BigNumber.from(1e12 + ''));
    }
    return {
      target: target,
      mined: mined,
      airdropClaimed: airdropClaimed,
      balanceForChef: balanceForChef,
      totalDepositTBTCAmount: totalDepositTBTCAmount,
      stakedTBTC: stakedTBTC,
      stakedTokenShrinkFactor: stakedTokenShrinkFactor
    };
  }, [TBTCChefContract, TBTCContract]);

   const chefDogeStats = useAsync<any>(async () => {
     const target = await TARGET_INITIAL_SUPPLY(TDOGEChefContract);
     const mined = await alreadyMinedReward(TDOGEChefContract);
     const airdropClaimed = await merkleTotalAlreadyClaimed(TDOGEChefContract);
     const balanceForChef = await TDOGEContract.balanceOf(
       contractConfig.TDOGE_CHEF
     );
     const poolInfo = await getPoolInfo(TDOGEChefContract, 3);
     const totalDepositTBTCAmount = poolInfo.totalDepositAmount;
     let stakedTBTC = totalDepositTBTCAmount;
     const stakedTokenShrinkFactor = poolInfo.stakedTokenShrinkFactor;
     if (stakedTokenShrinkFactor > 0) {
       stakedTBTC = stakedTBTC
         .mul(stakedTokenShrinkFactor)
         .div(BigNumber.from(1e12 + ''));
     }
     return {
       target: target,
       mined: mined,
       airdropClaimed: airdropClaimed,
       balanceForChef: balanceForChef,
       totalDepositTBTCAmount: totalDepositTBTCAmount,
       stakedTBTC: stakedTBTC,
       stakedTokenShrinkFactor: stakedTokenShrinkFactor
     };
   }, [TDOGEChefContract, TDOGEContract]);

  const rebaseRecords = useAsync<any>(async () => {
    const len = Number(
      BigNumber.from(
        await TBTCESTPolicyContract.getRebaseRecordsLen()
      ).toNumber()
    );
    const promiseArray = [];
    for (
      let index = len - 1;
      index >= 0 && len - index <= BATCH_LOAD_RECORD_LIMIT;
      index--
    ) {
      promiseArray.push(TBTCESTPolicyContract.rebaseRecords(index));
    }

    const list = await Promise.all(promiseArray);
    console.log('rebaseRecords', list);
    return list;
  }, [TBTCESTPolicyContract, account]);

  const rebaseDogeRecords = useAsync<any>(async () => {
    const len = Number(
      BigNumber.from(
        await TDOGEESTPolicyContract.getRebaseRecordsLen()
      ).toNumber()
    );
    const promiseArray = [];
    for (
      let index = len - 1;
      index >= 0 && len - index <= BATCH_LOAD_RECORD_LIMIT;
      index--
    ) {
      promiseArray.push(TDOGEESTPolicyContract.rebaseRecords(index));
    }

    const list = await Promise.all(promiseArray);
    console.log('rebaseDogeRecords', list);
    return list;
  }, [TDOGEESTPolicyContract, account]);

  const btcDailyRecords = useAsync<any>(() => {
    return getDailyRecords(
      FarmBTCContract,
      farmBTCBasicInfo.result,
      contractConfig.RTOKEN
    );
  }, [FarmBTCContract, farmBTCBasicInfo.result]);

  const btcstDailyRecords = useAsync<any>(async () => {
    return getDailyRecords(
      FarmBTCSTContract,
      farmBTCSTBasicInfo.result,
      contractConfig.STOKEN
    );
  }, [FarmBTCSTContract, farmBTCSTBasicInfo.result]);

  // useEffect(() => {
  //   console.log(
  //     'btcstTotalSupply',
  //     btcstTotalSupply.result,
  //     btcstTotalSupply.loading
  //   );
  // }, [btcstTotalSupply]);

  return {
    tvl,
    txInfo,
    tokenPrice,
    totalMinted,
    btcDailyRecords,
    btcstDailyRecords,
    farmBTCBasicInfo,
    farmBTCSTBasicInfo,
    tbtcTotalSupply,
    tdogeTotalSupply,
    btcstTotalSupply,
    rebaseRecords,
    rebaseDogeRecords,
    chefStats,
    chefDogeStats,
    BTCBPoolStakeAmount,
    BTCSTPoolStakeAmount,
    totalMinedBTC,
    totalMinedBTCST,
    totalInPoolBTC,
    apy
  };
};
