import React, { FC, useState, useCallback, ChangeEvent } from 'react';
import {
  Container,
  Grid,
  Typography,
  makeStyles,
  Tabs,
  Tab,
  Box
} from '@material-ui/core';
import Page from 'src/components/Page';
import { Theme } from 'src/theme';
import Header from 'src/components/Header';
import DailyMineRecord from './DailyMineRecord';
import TauBTCPrice2 from './TauBTCPrice2';
import BTCPrice from './BTCPrice';
import TauBTCInitMiningProgress from './TauBTCInitMiningProgress';
import TauBTCRebaseCountdown from './TauBTCRebaseCountdown';
import TauBTCTotalSupply from './TauBTCTotalSupply';
import OverviewMineDaily from './OverviewMineDaily';
import TauBTCRebaseRecord from './TauBTCRebaseRecord';
import Overview from './OverviewDApp';
import Overview2 from './Overview2';
import BTCSTMineBTCBRecord from './BTCSTMineBTCBRecord';
import TauBTCBasicMintRecord from './TauBTCBasicMintRecord';
import TauBTCAirdropSegmentation from './TauBTCAirdropSegmentation';
import { useStatsState } from 'src/hooks/useStatsState';
import { BigNumber } from '@ethersproject/bignumber';
import { useTranslation } from 'react-i18next';
import { BTCB, TBTC, BTCST, TDOGE, DOGE } from 'src/constants/token';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    // paddingTop: theme.spacing(3),
    paddingBottom: '80px'
  },
  title: {
    display: 'block',
    '@media (min-width: 1280px)': {
      display: 'none'
    }
  },
  tau: {
    'text-transform': 'initial'
  }
}));

const tabs = [TBTC.symbol, TDOGE.symbol];

const DashboardView: FC = () => {
  const classes = useStyles();
  const state = useStatsState();
  const { t } = useTranslation();
  const [currentTab, setCurrentTab] = useState(tabs[0]);

  const handleTabsChange = useCallback(
    (event: ChangeEvent<{}>, tab: string) => {
      setCurrentTab(tab);
    },
    []
  );

  return (
    <Page className={classes.root} title={t('global.title')}>
      <Container maxWidth={false}>
        <Header title={t('stats.tittle')} />
        <Grid container spacing={3}>
          <Grid item xs={12} className={classes.title}>
            <Typography variant="h3" color="textPrimary">
              {t('stats.tittle')}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Overview
              tvl={state.tvl}
              txInfo={state.txInfo}
              tokenPrice={state.tokenPrice}
              tbtcTotalSupply={state.tbtcTotalSupply}
              btcstTotalSupply={state.btcstTotalSupply}
            />
          </Grid>
          {/* <Grid item xs={12}>
            <Typography variant="h3" color="textPrimary">
              {t('stats.tbtc.title')}
            </Typography>
          </Grid> */}

          <Box mt={3} mb={3}>
            <Tabs
              onChange={handleTabsChange}
              scrollButtons="auto"
              value={currentTab}
              variant="scrollable"
              textColor="secondary"
            >
              {tabs.map(tab => (
                <Tab
                  key={tab}
                  label={t(`stats.${tab}.title`)}
                  value={tab}
                  style={{ textTransform: 'none' }}
                />
              ))}
            </Tabs>
          </Box>

          {currentTab === TBTC.symbol && (
            <Grid container spacing={3}>
              <Grid item lg={3} sm={6} xs={12}>
                <BTCPrice
                  title={
                    <>
                      <i className={classes.tau}>&tau;</i>
                      {t('stats.tbtc.price') + ' (TWAP)'}
                    </>
                  }
                  tokenPrice={state.tokenPrice}
                  token={TBTC}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <BTCPrice
                  title={t('stats.btc.price')}
                  tokenPrice={state.tokenPrice}
                  token={BTCB}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <TauBTCTotalSupply
                  totalSupply={state.tbtcTotalSupply}
                  chefStats={state.chefStats}
                  token={TBTC}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <TauBTCRebaseCountdown />
              </Grid>
              <Grid item xs={12}>
                <TauBTCInitMiningProgress
                  totalSupply={state.tbtcTotalSupply}
                  chefStats={state.chefStats}
                  initialMinted={BigNumber.from(21).mul(1e9)}
                  token={TBTC}
                />
              </Grid>

              <Grid item xs={12}>
                <TauBTCRebaseRecord
                  rebaseRecords={state.rebaseRecords}
                  token={TBTC}
                  tokenPriceList={state.tokenPrice}
                />
              </Grid>
            </Grid>
          )}

          {currentTab === TDOGE.symbol && (
            <Grid container spacing={3}>
              <Grid item lg={3} sm={6} xs={12}>
                <BTCPrice
                  title={
                    <>
                      <i className={classes.tau}>&tau;</i>
                      {t('stats.tdoge.price') + ' (TWAP)'}
                    </>
                  }
                  tokenPrice={state.tokenPrice}
                  token={TDOGE}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <BTCPrice
                  title={t('stats.doge.price')}
                  tokenPrice={state.tokenPrice}
                  token={DOGE}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <TauBTCTotalSupply
                  totalSupply={state.tdogeTotalSupply}
                  chefStats={state.chefDogeStats}
                  token={TDOGE}
                />
              </Grid>
              <Grid item lg={3} sm={6} xs={12}>
                <TauBTCRebaseCountdown />
              </Grid>
              <Grid item xs={12}>
                <TauBTCInitMiningProgress
                  totalSupply={state.tdogeTotalSupply}
                  chefStats={state.chefDogeStats}
                  initialMinted={BigNumber.from(21).mul(1e9)}
                  token={TDOGE}
                />
              </Grid>

              <Grid item xs={12}>
                <TauBTCRebaseRecord
                  rebaseRecords={state.rebaseDogeRecords}
                  token={TDOGE}
                  tokenPriceList={state.tokenPrice}
                />
              </Grid>
            </Grid>
          )}

          <Grid item xs={12}>
            <Typography variant="h3" color="textPrimary">
              {t('stats.btcst-btc.title')}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <OverviewMineDaily
              btcstTotalSupply={state.btcstTotalSupply}
              totalMined={state.totalMinedBTC}
              totalInPoolReward={state.totalInPoolBTC}
              staked={state.BTCBPoolStakeAmount}
              BTCBPoolStakeAmount={state.BTCBPoolStakeAmount}
              BTCSTPoolStakeAmount={state.BTCSTPoolStakeAmount}
              apy={state.apy}
              targetToken={BTCB}
            />
          </Grid>
          <Grid item xs={12}>
            <DailyMineRecord
              title={t('stats.earnBTCDaily')}
              apy={state.apy}
              loading={
                state.farmBTCBasicInfo.loading || state.btcDailyRecords.loading
              }
              records={state.btcDailyRecords.result}
              stake={state.BTCBPoolStakeAmount}
              targetToken={BTCB}
            />
          </Grid>
          <Grid item xs={12}>
            <Typography variant="h3" color="textPrimary">
              {t('stats.btcst-btcst.title')}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <OverviewMineDaily
              btcstTotalSupply={state.btcstTotalSupply}
              totalMined={state.totalMinedBTCST}
              totalInPoolReward={state.totalInPoolBTC}
              staked={state.BTCSTPoolStakeAmount}
              BTCBPoolStakeAmount={state.BTCBPoolStakeAmount}
              BTCSTPoolStakeAmount={state.BTCSTPoolStakeAmount}
              apy={state.apy}
              targetToken={BTCST}
            />
          </Grid>
          <Grid item xs={12}>
            <DailyMineRecord
              title={t('stats.earnBTCSTDaily')}
              apy={state.apy}
              loading={
                state.farmBTCSTBasicInfo.loading ||
                state.btcstDailyRecords.loading
              }
              records={state.btcstDailyRecords.result}
              stake={state.BTCSTPoolStakeAmount}
              targetToken={BTCST}
            />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default DashboardView;
